/**
 * Rem-converter mixin to convert px to rem
 */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body {
  width: 100%;
  height: 100%;
}

body {
  background-color: white;
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  font-family: "Montserrat", sans-serif;
}

img {
  display: block;
  height: auto;
  max-width: 100%;
}

ul, ol {
  list-style: none;
}

a {
  text-decoration: none;
}

header,
footer {
  flex: 0 0 auto;
}

.wrapper {
  /* 1 flex-grow, 0 flex-shrink, auto flex-basis */
  flex: 1 0 auto;
  position: relative;
}

h2,
h3, h4 {
  color: #005f9f;
}

.container {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
}

section {
  margin-bottom: 170px;
}
section h2 {
  font-size: 90px;
  font-size: 5.625rem;
  font-weight: 500;
}

.page_section {
  margin-top: 80px;
}
.page_section h2 {
  font-size: 70px;
  font-size: 4.375rem;
}

#map {
  height: 100%;
}

header {
  padding-top: 40px;
}
header .header {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav-top {
  width: 441px;
  -webkit-transition: all 0.3s linear;
  -moz-transition: all 0.3s linear;
  -o-transition: all 0.3s linear;
  transition: all 0.3s linear;
}
.nav-top ul li {
  display: block;
  margin-right: 40px;
}
.nav-top ul li:last-child {
  margin: 0;
}

.nav-footer {
  width: 470px;
}
.nav-footer ul {
  justify-content: center;
}

nav ul.menu {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
nav ul.menu li {
  display: block;
  margin-right: 40px;
}
nav ul.menu a {
  font-size: 18px;
  font-size: 1.125rem;
  font-weight: 500;
  color: #011e32;
}
nav ul.menu a:hover {
  color: #005f9f;
}
nav ul.menu a.current {
  color: #005f9f;
}

.promo_block {
  margin-top: 110px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.promo_block .promo_text h1 {
  font-size: 50px;
  font-size: 3.125rem;
  font-weight: 500;
}
.promo_block .promo_text p {
  font-size: 24px;
  font-size: 1.5rem;
  line-height: 37px;
  margin: 30px 0 54px 0;
}
.promo_block .promo_img {
  max-width: 530px;
  width: 100%;
  justify-self: end;
}

.blocks {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 0.8em;
  grid-row-gap: 5em;
  margin: 50px 0 0;
}
.blocks .block {
  background-color: #f6f6f6;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  border-radius: 10px;
  height: 246px;
  display: grid;
  justify-items: center;
  align-items: center;
}
.blocks .block p {
  font-size: 24px;
  font-size: 1.5rem;
  font-weight: 400;
  line-height: 37px;
  padding: 0 34px;
}
.blocks .block p:before {
  content: "";
  border: 2px solid;
  position: relative;
  left: -10%;
  border-radius: 10px;
  color: #005f9f;
}

.technology_block {
  margin-top: 70px;
}
.technology_block h3 {
  font-size: 40px;
  font-size: 2.5rem;
  line-height: 49px;
  font-weight: 500;
  margin-bottom: 55px;
}
.technology_block .technologies,
.technology_block .clients {
  display: grid;
  grid-row-gap: 4em;
}
.technology_block .technologies .tech,
.technology_block .clients .tech {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  align-items: center;
}
.technology_block .technologies .tech span,
.technology_block .clients .tech span {
  font-size: 28px;
  font-size: 1.75rem;
  line-height: 43px;
  font-weight: 400;
  color: #011e32;
  margin-left: 20px;
}
.technology_block .technologies {
  grid-template-columns: repeat(4, 1fr);
}
.technology_block .clients {
  grid-template-columns: repeat(5, 1fr);
}

.info_block {
  display: grid;
  grid-template-columns: 28% repeat(3, 1fr);
  margin-top: 50px;
}
.info_block .contact_block .block {
  background-color: #f6f6f6;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  border-radius: 10px;
  margin-bottom: 10px;
  padding: 0 34px;
  width: 100%;
  height: 275px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.info_block .contact_block .block:last-child {
  margin: 0;
}
.info_block .contact_block .block p, .info_block .contact_block .block a {
  font-size: 20px;
  font-size: 1.25rem;
  font-weight: 400;
  line-height: 35px;
  margin-bottom: 24px;
}
.info_block .contact_block .block p:last-child, .info_block .contact_block .block a:last-child {
  margin: 0;
}
.info_block .contact_block .block p:before {
  content: "";
  border: 2px solid;
  position: relative;
  left: -34px;
  border-radius: 10px;
  color: #005f9f;
}
.info_block .contact_block .block a {
  color: #005f9f;
}
.info_block .contact_block .block a:visited {
  color: #005f9f;
}
.info_block .contact_block .block a.contact_phone {
  font-size: 28px;
  font-size: 1.75rem;
  font-weight: 500;
}
.info_block .map_block {
  grid-column: span 3;
  margin-left: 20px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  border-radius: 10px;
  border: 1px solid #f6f6f6;
  overflow: hidden;
}

section .service_block {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin-bottom: 130px;
}
section .service_block .service_img {
  justify-self: center;
}
section .service_block .service_text h3 {
  margin-bottom: 30px;
  font-size: 40px;
  font-size: 2.5rem;
  color: #011e32;
}
section .service_block .service_text p {
  font-size: 18px;
  font-size: 1.125rem;
  line-height: 28px;
  margin-bottom: 30px;
}
section .service_block:nth-child(even) .service_img {
  order: 2;
}
section .service_block:nth-child(even) .service_text {
  order: 1;
}
section .service_block:nth-child(odd) .service_img {
  order: 1;
}
section .service_block:nth-child(odd) .service_text {
  order: 2;
}

.clients_block {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 82px;
  grid-row-gap: 100px;
  margin-top: 70px;
}
.clients_block .client_block img {
  margin: 0 auto 30px;
}
.clients_block .client_block p {
  font-size: 18px;
  font-size: 1.125rem;
  line-height: 28px;
  font-weight: 400;
}

.testimonials_block {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 10px;
  margin-top: 50px;
}
.testimonials_block .testimonial_block {
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  border-radius: 10px;
  background-color: #f6f6f6;
  padding: 40px 34px;
  height: 386px;
}
.testimonials_block .testimonial_block p {
  font-size: 18px;
  font-size: 1.125rem;
  font-weight: 400;
  line-height: 28px;
}
.testimonials_block .testimonial_block .user {
  display: flex;
  align-items: center;
}
.testimonials_block .testimonial_block .user img {
  margin-right: 20px;
}
.testimonials_block .testimonial_block .user p {
  font-size: 16px;
  font-size: 1rem;
  font-weight: 400;
}
.testimonials_block .testimonial_block .user span {
  display: block;
  font-size: 14px;
  font-size: 0.875rem;
  font-weight: 400;
  color: #879198;
  margin-top: 4px;
}
.testimonials_block .testimonial_block h3 {
  margin-top: 20px;
  margin-bottom: 10px;
  font-weight: 500;
  font-size: 18px;
  font-size: 1.125rem;
  line-height: 28px;
}
.testimonials_block .testimonial_block h3:before {
  content: "";
  border: 2px solid;
  position: relative;
  left: -34px;
  border-radius: 10px;
  color: #005f9f;
}

.page_section h2 {
  margin-bottom: 50px;
}

.about_block {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
}
.about_block .img_block {
  justify-content: center;
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  flex: 2 1 60%;
  margin-right: 50px;
}
.about_block aside {
  flex: 2 2 30%;
}
.about_block aside .block {
  background-color: #f6f6f6;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  border-radius: 10px;
  display: grid;
  align-items: center;
  height: 246px;
  margin-bottom: 20px;
}
.about_block aside .block p {
  font-size: 20px;
  font-size: 1.25rem;
  font-weight: 400;
  line-height: 37px;
  padding: 0 34px;
}
.about_block aside .block p:before {
  content: "";
  border: 2px solid;
  position: relative;
  left: -34px;
  border-radius: 10px;
  color: #005f9f;
}

.questions_block {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  margin-top: 130px;
}
.questions_block .question h3,
.questions_block .contact_block h3 {
  font-size: 70px;
  font-size: 4.375rem;
  font-weight: 500;
  margin-bottom: 50px;
}
.questions_block .question {
  flex: 2 1 60%;
  margin-right: 50px;
}
.questions_block .question .question_contet {
  margin-bottom: 70px;
}
.questions_block .question .question_contet h4 {
  font-size: 40px;
  font-size: 2.5rem;
  color: #011e32;
  line-height: 60px;
  font-weight: 500;
  margin-bottom: 30px;
}
.questions_block .question .question_contet p {
  font-size: 18px;
  font-size: 1.125rem;
  line-height: 28px;
}
.questions_block .contact_block {
  flex: 1 2 30%;
}
.questions_block .contact_block .block {
  background-color: #f6f6f6;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  border-radius: 10px;
  margin-bottom: 10px;
  padding: 0 34px;
  width: 100%;
  height: 275px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.questions_block .contact_block .block:last-child {
  margin: 0;
}
.questions_block .contact_block .block p, .questions_block .contact_block .block a {
  font-size: 24px;
  font-size: 1.5rem;
  font-weight: 400;
  line-height: 35px;
  margin-bottom: 24px;
}
.questions_block .contact_block .block p:last-child, .questions_block .contact_block .block a:last-child {
  margin: 0;
}
.questions_block .contact_block .block p:before {
  content: "";
  border: 2px solid;
  position: relative;
  left: -34px;
  border-radius: 10px;
  color: #005f9f;
}
.questions_block .contact_block .block a {
  color: #005f9f;
}
.questions_block .contact_block .block a:visited {
  color: #005f9f;
}
.questions_block .contact_block .block a.contact_phone {
  font-weight: 500;
  font-size: 32px;
  font-size: 2rem;
}

.tabs_block {
  display: grid;
  grid-template-columns: 500px repeat(1, 1fr);
  grid-column-gap: 60px;
  margin-top: 50px;
}

.tabs {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #f6f6f6;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  border-radius: 10px;
  height: 340px;
}

.tabs li:not(:last-child) {
  margin-right: 7px;
}

.tabs li a {
  display: block;
  position: relative;
  top: 4px;
  padding: 10px 25px;
  border-radius: 2px 2px 0 0;
  opacity: 0.7;
  transition: all 0.1s ease-in-out;
  color: #011e32;
  font-size: 24px;
  font-size: 1.5rem;
  line-height: 38px;
}

.tabs li.active a {
  font-weight: 500;
}
.tabs li.active a:before {
  content: "";
  border: 2px solid;
  position: relative;
  left: -6%;
  border-radius: 10px;
}

.tabs li.active a,
.tabs li a:hover {
  opacity: 1;
  color: #005f9f;
}

.tabs-content {
  position: relative;
  z-index: 2;
}

.tabs-panel {
  display: none;
}

.tabs-panel.active {
  display: block;
  animation: scale 0.7s ease-in-out;
}
.tabs-panel.active h3 {
  color: #011e32;
  font-size: 40px;
  font-size: 2.5rem;
  font-weight: 500;
}
.tabs-panel.active p {
  font-size: 18px;
  font-size: 1.125rem;
  line-height: 38px;
  margin-top: 30px;
}

.tabs-panel p + div {
  margin-top: 15px;
}

@keyframes scale {
  0% {
    transform: scale(0.9);
    opacity: 0;
  }
  50% {
    transform: scale(1.005);
    opacity: 0.5;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}
footer {
  background-color: #f6f6f6;
}
footer .footer {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 60px 0 30px 0;
}
footer .footer .logo {
  width: 72px;
  height: 60px;
}
footer .footer .footer_icon ul {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
}
footer .footer .footer_icon ul li {
  margin-right: 30px;
}
footer .footer .footer_icon ul li:last-child {
  margin: 0;
}
footer .footer .footer_icon ul li a {
  display: block;
  width: 24px;
  height: 24px;
}
footer .copyright {
  width: 270px;
  margin: 30px auto;
}
footer .copyright span {
  font-size: 17px;
  font-size: 1.0625rem;
  font-weight: 500;
  color: #879198;
}

/* On screens that are 1000px or less */
@media screen and (max-width: 1000px) {
  body {
    /* background-color: blue; */
  }

  .wrapper,
footer {
    padding: 0 15px;
  }

  .promo_block .promo_text h1 {
    font-size: 40px;
    font-size: 2.5rem;
  }

  .promo_block .promo_text p {
    font-size: 19px;
    font-size: 1.1875rem;
  }

  .blocks .block p {
    font-size: 18px;
    font-size: 1.125rem;
  }
  .blocks .block p:before {
    left: -34px;
  }

  .tabs_block {
    grid-template-columns: 300px repeat(1, 1fr);
  }
  .tabs_block .tabs li a {
    font-size: 16px;
    font-size: 1rem;
  }
  .tabs_block .tabs li.active a:before {
    left: -25px;
  }

  /* Clients */
  .clients_block {
    grid-template-columns: repeat(2, 1fr);
  }
  .clients_block .client_block img {
    margin: 0 0 30px;
  }

  .testimonials_block .testimonial_block {
    height: 450px;
  }

  .info_block {
    grid-template-columns: 32% repeat(3, 1fr);
  }
}
/* On screens that are 768px or less */
@media screen and (max-width: 768px) {
  section {
    margin-bottom: 130px;
  }
  section h2 {
    font-size: 70px;
    font-size: 4.375rem;
  }

  .promo_block {
    grid-template-columns: repeat(2, 1fr);
  }
  .promo_block .promo_text h1 {
    font-size: 36px;
    font-size: 2.25rem;
  }
  .promo_block .promo_text p {
    font-size: 21px;
    font-size: 1.3125rem;
  }

  .blocks {
    grid-template-columns: repeat(1, 1fr);
    grid-row-gap: 0.5em;
  }
  .blocks .block {
    height: 162px;
  }
  .blocks .block p {
    font-size: 20px;
    font-size: 1.25rem;
    line-height: 31px;
  }
  .blocks .block p:before {
    left: -34px !important;
  }

  .technology_block .technologies {
    grid-template-columns: repeat(3, 1fr);
  }
  .technology_block .technologies .tech span {
    font-size: 24px;
    font-size: 1.5rem;
    font-weight: 400;
    line-height: 37px;
  }
  .technology_block .clients {
    grid-template-columns: repeat(3, 1fr);
  }

  .tabs_block {
    display: block;
  }

  .tabs {
    flex-direction: row;
    height: inherit;
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 10px 20px;
    margin-bottom: 40px;
  }

  .tabs li {
    width: 100%;
  }

  .tabs li:not(:last-child) {
    margin-right: 0;
  }

  .tabs li a {
    border-radius: 0;
    opacity: 1;
    top: 0;
    line-height: 23px;
    font-size: 14px;
    font-size: 0.875rem;
    padding: 10px 0;
  }

  .tabs li.active a::before {
    content: " ";
    padding-right: 2px;
    border: 1px solid;
    left: -22px !important;
    background: #005f9f;
  }

  .tabs-content {
    border-radius: 0;
  }
  .tabs-content .tabs-panel.active p {
    margin-top: 20px;
  }

  .info_block {
    grid-template-columns: 1fr;
  }
  .info_block .contact_block {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 20px;
  }
  .info_block .contact_block .block {
    height: 200px;
  }
  .info_block .map_block {
    margin-left: 0;
    min-height: 560px;
    height: 100%;
  }

  section .service_block {
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 40px;
  }
  section .service_block .service_img {
    align-content: center;
    display: grid;
  }

  .colored {
    margin: 0 auto;
  }

  .clients_block {
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 40px;
  }

  .page_section h2 {
    font-size: 50px;
    font-size: 3.125rem;
  }

  .testimonials_block {
    grid-template-columns: repeat(2, 1fr);
  }
  .testimonials_block .testimonial_block {
    height: 554px;
  }

  /* About page */
  .page_section h2 {
    font-size: 70px;
    font-size: 4.375rem;
    text-align: center;
  }

  .about_block {
    flex-direction: column;
  }
  .about_block .img_block {
    margin-right: 0;
  }
  .about_block .img_block img {
    margin: 0 auto;
  }
  .about_block aside .block {
    height: 162px;
  }
  .about_block aside .block p {
    font-size: 20px;
    font-size: 1.25rem;
  }

  .questions_block {
    flex-direction: column;
  }
  .questions_block .contact_block {
    flex: 1 1 100%;
  }
  .questions_block .contact_block .blocks_with_contact {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 10px;
  }

  .nav-footer ul.menu {
    margin: 40px 0 70px;
    justify-content: center;
  }
  .nav-footer ul.menu li {
    margin-right: 30px;
  }
  .nav-footer ul.menu li:last-child {
    margin-right: 0;
  }
}
/* On screens that are 600px or less */
@media screen and (max-width: 600px) {
  section h2 {
    font-size: 70px;
    font-size: 4.375rem;
  }

  .technology_block h3 {
    font-size: 35px;
    font-size: 2.1875rem;
  }

  .promo_block {
    grid-template-columns: repeat(1, 1fr);
  }
  .promo_block .promo_text {
    order: 2;
  }

  .technology_block .technologies {
    grid-template-columns: repeat(2, 1fr);
  }

  .tabs-panel.active h3 {
    font-size: 35px;
    font-size: 2.1875rem;
  }

  .technology_block .clients {
    grid-template-columns: repeat(3, 1fr);
  }

  .nav-footer {
    width: auto;
  }
}
/* On screens that are 480px or less */
@media screen and (max-width: 480px) {
  section h2 {
    font-size: 48px;
    font-size: 3rem;
    text-align: center;
    line-height: 59px;
  }

  header .header {
    display: block;
    /*defining the hamburger*/
  }
  header .header #hamburger {
    display: block;
    position: absolute;
    height: 18px;
    width: 26px;
    right: 10%;
    top: 1.5%;
    transform: translateY(-50%);
    z-index: 200;
  }
  header .header #hamburger .line {
    width: 36px;
    height: 2px;
    display: block;
    background-color: #005f9f;
    position: absolute;
    left: 0;
    transition: all 0.3s cubic-bezier(0.58, 0, 0.58, 1);
  }
  header .header #hamburger .line:nth-child(1) {
    top: 0;
    width: 26px;
  }
  header .header #hamburger .line:nth-child(2) {
    top: 50%;
    margin-top: -1px;
    width: 26px;
  }
  header .header #hamburger .line:nth-child(3) {
    top: 100%;
    margin-top: -2px;
    width: 26px;
  }
  header .header #hamburger:hover .line:nth-child(1),
header .header #hamburger:focus .line:nth-child(1) {
    transform: translateY(-1px);
  }
  header .header #hamburger:hover .line:nth-child(3),
header .header #hamburger:focus .line:nth-child(3) {
    transform: translateY(1px);
  }
  header .header #hamburger.hamburger__open .line:nth-child(1) {
    transform: translateY(8px) rotate(45deg);
    width: 26px;
  }
  header .header #hamburger.hamburger__open .line:nth-child(2) {
    opacity: 0;
  }
  header .header #hamburger.hamburger__open .line:nth-child(3) {
    transform: translateY(-8px) rotate(-45deg);
  }

  .nav-top {
    position: absolute;
    width: 100%;
    z-index: 100;
    background: white;
    opacity: 0;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    transform: translateY(-100%);
  }

  .nav-top.open {
    display: block;
    transform: translateY(-1%);
    opacity: 1;
  }
  .nav-top.open .menu {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 17%;
  }
  .nav-top.open .menu li {
    width: 100%;
    margin-right: 0;
    margin-bottom: 50px;
    text-align: center;
  }
  .nav-top.open .menu li a {
    font-size: 20px;
    font-size: 1.25rem;
    text-transform: uppercase;
    font-weight: 500;
  }

  .promo_block .promo_text h1 {
    font-size: 30px;
    font-size: 1.875rem;
  }
  .promo_block .promo_text p {
    font-size: 18px;
    font-size: 1.125rem;
    line-height: 28px;
    margin: 30px 0 60px;
  }

  .blocks .block {
    height: 193px;
  }

  .technology_block h3,
.tabs-panel.active h3 {
    font-size: 28px;
    font-size: 1.75rem;
    line-height: 34px;
  }

  .tabs-panel.active p {
    font-size: 15px;
    font-size: 0.9375rem;
    line-height: 27px;
  }

  .technology_block h3,
.tabs-panel.active h3 {
    font-size: 24px;
    font-size: 1.5rem;
  }

  .technology_block h3 {
    line-height: 32px;
    text-align: center;
  }

  .technology_block .technologies .tech {
    display: inline;
  }
  .technology_block .technologies .tech img {
    margin: 0 auto;
  }
  .technology_block .technologies .tech span {
    margin: 16px 0;
    display: block;
    text-align: center;
  }
  .technology_block .clients {
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 57px;
  }

  .testimonials_block {
    grid-template-columns: repeat(1, 1fr);
  }

  .info_block .contact_block {
    grid-template-columns: repeat(1, 1fr) !important;
  }
  .info_block .map_block {
    margin-top: 10px;
  }

  /* Services */
  section .service_block {
    display: block;
    margin-bottom: 100px;
  }
  section .service_block .service_img img {
    margin: 0 auto 30px;
  }
  section .service_block .service_text h3 {
    font-size: 32px;
    font-size: 2rem;
  }
  section .service_block .service_text p {
    font-size: 15px;
    font-size: 0.9375rem;
    line-height: 23px;
  }
  section .service_block:nth-child(even) .service_img {
    order: 1;
  }
  section .service_block:nth-child(even) .service_text {
    order: 2;
  }
  section .service_block:nth-child(odd) .service_img {
    order: 2;
  }
  section .service_block:nth-child(odd) .service_text {
    order: 1;
  }

  /* Clients */
  .page_section h2 {
    font-size: 28px;
    font-size: 1.75rem;
    line-height: 38px;
  }

  .clients_block {
    grid-template-columns: repeat(1, 1fr);
    grid-row-gap: 60px;
  }
  .clients_block .client_block img {
    margin: 0 auto 20px;
  }
  .clients_block p {
    text-align: center;
  }

  /* About */
  .questions_block {
    flex-direction: column;
  }
  .questions_block .question {
    margin-right: 0;
    flex: none;
  }
  .questions_block .question .question_contet h4 {
    font-size: 27px;
    font-size: 1.6875rem;
    line-height: 46px;
    font-weight: 500;
  }
  .questions_block .contact_block .block {
    height: 236px;
  }
  .questions_block .contact_block .blocks_with_contact {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-column-gap: 10px;
  }

  footer .footer {
    flex-direction: column;
  }
}
/* On screens that are 320px or less */
@media screen and (max-width: 320px) {
  /* Service */
  section .service_block .service_text h3 {
    font-size: 28px;
    font-size: 1.75rem;
  }
  section .service_block .service_text p {
    font-size: 15px;
    font-size: 0.9375rem;
    line-height: 23px;
  }
}